import * as wx from "../node_modules/weixin-js-sdk/index.js";
import { setDevelopmentMode, WebBridgeApi } from "ijsbridge"
import { WebToolkitApiConstants } from './define';
console.log(wx, WebBridgeApi, 11111);


// 用户代理变量
const ua: string = window.navigator.userAgent.toLowerCase();

/**
 * @name: 判断是否在微信浏览器中
 * @test: 
 * @msg: 
 * @param {*} boolean
 * @return {*}
 */
const isWeiXin = (): boolean => {
    const match = ua.match(/MicroMessenger/i);
    if (match === null) {
        return false;
    }
    if (match.includes('micromessenger')) {
        return true;
    }
    return false;
}

/**
 * @name: 判断是否在app中
 * @test: 
 * @msg: 
 * @param {*} boolean
 * @return {*}
 */
const isApp = (): boolean => {
    return ua.indexOf("chuangqi") != -1 ? true : false
}

/**
 * @name: 判断是否在小程序中
 * @test: 
 * @msg: 
 * @param {*} boolean
 * @return {*}
 */
const isWeChatApplet = (): boolean => {
    if (ua.indexOf('micromessenger') == -1) {//不在微信或者小程序中
        return false
    } else {
        wx.miniProgram.getEnv((res: { miniprogram: any; }) => {
            if (res.miniprogram) {//在小程序中
                return true
            } else {//在微信中
                return false
            }
        });
        return false
    }
}



/**
 * @name: 用户类
 * @test: 
 * @msg: 
 * @param {*}
 * @return {*}
 */
class Platform {

    private env: WebToolkitApiConstants

    constructor() {
        this.env = this.isPlatform()
    }

    /**
         * @name: 判断当前环境
         * @test: 
         * @msg: 
         * @param {*}
         * @return {*}
         */
    isPlatform(): WebToolkitApiConstants {
        if (isWeiXin()) {
            return WebToolkitApiConstants.H5
        }
        else if (isApp()) {
            return WebToolkitApiConstants.AppH5
        }
        else if (isWeChatApplet()) {
            return WebToolkitApiConstants.MpWeixin
        }
        else { // 普通h5
            return WebToolkitApiConstants.H5
        }
    }

    /**
     * @name: 获取环境
     * @test: 
     * @msg: 
     * @param {*}
     * @return {*}
     */
    getPlatform(): WebToolkitApiConstants {
        return this.env
    }

}

export default Platform