import Platform from './Platform1'
import { setDevelopmentMode, WebBridgeApi } from "ijsbridge"
import { WebToolkitApiConstants } from './define';

const platform: Platform = new Platform()

const env: WebToolkitApiConstants = platform.getPlatform()

console.log(env, '当前环境');


/**
 * @name: 用户信息返回类型
 * @test: 
 * @msg: 
 * @param {*}
 * @return {*}
 */
type ResultObject = {
    err: string,
    code: number,
    data: any
}


/**
* @name: 获取用户信息接口
* @test: 
* @msg: 如果h5 则在缓存中找,一般是token和orgid
* 如果在一线一端中则是调用桥获取用户信息
* @param {*}
* @return {*}
*/
const getUserInfo = async (): Promise<ResultObject> => {
    let responce: ResultObject
    if (env === WebToolkitApiConstants.H5) {
        const result = await getAllObjCookie()
        return new Promise((resolve, reject) => {
            if (JSON.stringify(result) === '{}') {
                console.log('则没有用户信息');
                responce = {
                    err: '用户信息为空',
                    data: null,
                    code: 0
                }
                resolve(responce)
            }
            else {
                responce = {
                    err: '获取用户信息成功',
                    data: result,
                    code: 1
                }
                resolve(responce)
                console.log(result, '用户信息');
            }
        })
    }

    else if (env === WebToolkitApiConstants.AppH5) {
        return new Promise(async (resolve, reject) => {
            let { err, result } = await WebBridgeApi.getUserInfo({})
            console.log(err, result, '用户信息');
            responce = {
                err: '',
                data: '',
                code: 0
            }
            return responce
        })
    }

    else {
        responce = {
            err: '',
            data: '',
            code: 0
        }
        return responce
    }
}



/**
 * @name: 获取所有的cookie,后期可以维护成传入指定的键数组获取cookie
 * @test: 
 * @msg: 
 * @param {*}
 * @return {*}
 */
const getAllObjCookie = async (): Promise<object> => {
    let token = await getCookie('TOKEN')
    let orgid = await getCookie('orgid')
    return Object.assign({}, token, orgid)
}

/**
 * @name: 获取cookie信息对象形式
 * @test:
 * @msg:
 * @param {*}
 * @return {*}
 */
const getCookie = (name: string): Promise<object | null> => {

    return new Promise((resolve, reject) => {

        let obj: object = {}

        var prefix = name + "="
        var start = document.cookie.indexOf(prefix)

        if (start == -1) {
            resolve(null)
        }

        var end = document.cookie.indexOf(";", start + prefix.length)

        if (end == -1) {
            end = document.cookie.length;
        }

        var value = document.cookie.substring(start + prefix.length, end)

        obj[name] = unescape(value)
        resolve(obj)
    })

}

export default getUserInfo
